import React, {useState, useEffect} from "react";
import './Home.css';
//import  {useState, useEffect} from "react";

function Search(p){
    const [dataitems,setDataitems]=useState([]);
    const [val1,setVal1]=useState("");
    const [valcount,setValcount]=useState("");

    
    var s={}
    
       if (p.match){
        if (p.match.params){
            s.val=p.match.params.val
         }
       }
       if ( s.val  ){
           s.val= Array.isArray(s.val)?s.val[0]:s.val
           if (val1!==s.val){
            setVal1(s.val)
           }
       }
    //
    function get_da(i){
        if (!i && i!==0){return}
        if (i.length <2){
            setDataitems([ ]);
            return

        }
        function re(a){return " !~ "+a+" !~ "}
        var rr=RegExp(i,"gi")
        fetch(`/search?text=${i}`, { 
            method: 'GET',
            headers: {
              'Content-Type': 'application/json',
            }
          }).then( res=>{
            setValcount(0)

            if(!res.ok) {
                setDataitems(['error '+JSON.stringify(res)]);

            } else{
                res.json().then(r=>{
                    for (let v of r){
                    v.text=v.text.replace(rr,re).split(" !~ ").map((it,i)=>i===0 || i%2===0 ?it:<em>{it}</em>)
                    }
                    setDataitems(r)
                    setValcount(r.length)
                });
            }
            
        });
    
    }
    
     useEffect( () => {
        get_da(val1) 
    },[val1]);
    return(
        <div className="main-wrap">         
            <div className="dataitems-val">{val1}:&nbsp; {valcount}</div>
            <div className="dataitems-wrap search-wrap">
                {dataitems.map(c=>(
                    <div className="dataitems" data-id={c.surah}>
                        <div className="datahdr">
                            <div className="cat">{c.surah}</div>
                            <div className="num">{c.section}</div>
                        </div>
                        <div className="txt">
                            {c.text}
                        </div>
                  </div>
                ))}
            </div>
           
        </div>
    )
}



export default Search;