import React, {useState, useEffect} from "react";
import './Home.css';

function Home(p){
    const [categories,setCategories]=useState([]);
    const [dataitems,setDataitems]=useState([]);
    const [category,setCategory]=useState([]);

    console.log("AAAA")
    function get_da(i){
        if (!i && i!==0){return}
        console.log("yyyyy")

        fetch(`/category?id=${i}`, { 
            method: 'GET',
            headers: {
              'Content-Type': 'application/json',
            }
          }).then( res=>{
            if(!res.ok) {
                setDataitems(['error '+JSON.stringify(res)]);
            } else{
                console.log("xxx")
                res.json().then(r=>{
                    setDataitems(r)
                });
            }
            
        });
    
    }
   
    const onChangeCategory = (e)=>{
        setCategory(e.target.dataset.id)
    }
    const onChangedataitems=(e)=>{

    }
    function get_data(){
            
        fetch("/home", { 
            method: 'GET',
            headers: {
              'Content-Type': 'application/json',
            }
          }).then( res=>{
            if(!res.ok) {
                setCategories(['error '+JSON.stringify(res)]);
            } else{
                res.json().then(r=>{
                    setCategories(r)
                });
            }
            
        });
    }
    useEffect( () => {
        get_data() 
    },[]);
    useEffect( () => {
        get_da(category) 
    },[category]);
    return(
        <div className="main-wrap">
            <div className="category-wrap" onClick={onChangeCategory}>
            {categories.map(c=>(
                <div className="category" data-id={c.surah}>{c.label}</div>
            ))}
            </div>
            <div className="dataitems-wrap" onClick={onChangedataitems}>
            {dataitems.map(c=>(
                <div className="dataitems" data-id={c.surah}>
                    <div className="datahdr">
                        <div className="cat">{c.surah}</div>
                        <div className="num">{c.section}</div>

                    </div>
                    <div className="txt" >
                    {c.text}
                    </div>
                </div>
            ))}
            </div>
           
        </div>
    )
}


export default Home;